.profile__avatar-edit {
  margin: 0;
  padding: 0;
  display: none;
  background: url(../../../images/editAvatar.svg) no-repeat center center;
  background-size: 26px 26px;
  width: 120px;
  height: 120px;
  position: absolute;
  top: 0px;
  left: 0px;
  transition: display .5s ease;
}
